import React from 'react'
import {connect} from 'react-redux'
import * as R from 'ramda'
import del from './del.png'
import {
    getBasketPhonesWithCount,
    getTotalBasketPrice
} from '../../selectors.js'
import {Link} from "react-router-dom";
import {
    removePhoneFromBasket,
    removeOnePhoneFromBasket,
    addPhoneFromBasket,
    sendOrder
} from '../../actions/index.js'

const Basket = ({
                    phones, totalPrice, removePhoneFromBasket,
                    addPhoneFromBasket, removeOnePhoneFromBasket, sendOrder
                }) => {
    const isBasketEmpty = R.isEmpty(phones)

    const renderPrice = (phone) => {
        if (phone.special_price)
            return (
                <div>
                    <p className='price'><span className='new-price'>{phone.special_price} грн</span></p>
                </div>
            )
        return (
            <div>
                <p className='price'><span className='new-price'>{phone.price}</span> грн</p>
            </div>
        )
    }
    const renderContent = () => {
        return (
            <div>
                {isBasketEmpty && <div className='empty-basket lower'>Ваша корзина порожня, <Link className='link-tostore' to='/'>повернутись до товарів</Link></div>}

                <div className='items-grid'>
                    {phones.map((phone, index) => (
                        <div className='item-cell lower' key={index}>
                            <div className='img-wrapper'>
                                <img
                                    className='item-img'
                                    src={phone.image_url}
                                    alt={phone.name}
                                />
                            </div>
                            <div className='wrapper'>
                                {renderPrice(phone)}
                            </div>
                            <div className='wrapper'>
                                <p>{phone.count} шт</p>
                            </div>
                            <div className='wrapper'>
                                <Link to={`/phones/${phone.id}`} className='link-phone'>
                                    <p className='link-text'>{phone.name}</p>
                                </Link>
                            </div>
                            <div className='wrapper basket-btns'>
                                <Link to={`/phones/${phone.id}`} className='link-phone'>
                                    <button className='add-btn bk-btn'>
                                        <p className='smaller-link'>Детальніше про товар</p>
                                    </button>
                                </Link>
                                {/*<Link className='link-phone'>*/}
                                    {/*<Button/>*/}
                                    <button
                                        className='add-btn del add-link'
                                        onClick={() => {
                                            addPhoneFromBasket(phone.id)
                                        }}
                                    >
                                        +
                                    </button>
                                {/*</Link>*/}
                                {/*<Link className='link-phone'>*/}
                                    <button
                                        className='add-btn del remove-link'
                                        onClick={() => {
                                            removeOnePhoneFromBasket(phone.id)
                                        }}
                                    >
                                        -
                                    </button>
                                {/*</Link>*/}
                                {/*<Link className='link-phone'>*/}
                                    <button
                                        className='add-btn del del-link'
                                        onClick={() => {
                                            removePhoneFromBasket(phone.id)
                                        }}
                                    >
                                        <img className='del-img' src={del} alt='Видалити'/>
                                    </button>
                                {/*</Link>*/}
                            </div>
                        </div>
                    ))}
                </div>
            </div>
        )
    }

    const renderSidebar = () => (
        <div className='sidebar'>
            <div className='basket-n'>
                <Link to='/' className='category'>
                    <button className='nav-btn'>
                        <p className='category-text back'>Назад</p>
                    </button>
                </Link>
                <div className='category'>
                    <p className='category-text back'>Всього: {totalPrice} грн</p>
                </div>
                {/*<Link className='category'>*/}
                    <button className='nav-btn'
                            onClick={() => {
                                console.log(isBasketEmpty)
                                !isBasketEmpty&&document.getElementById('order').classList.remove('hidden')
                            }}>
                        <p className='category-text back bnav-btn'>Замовлення</p>
                    </button>
                {/*</Link>*/}
            </div>
        </div>
    )

    return (
        <div>
            <div>
                {renderContent()}
            </div>
            <div>
                {renderSidebar()}
            </div>
            <div className='hidden modal' id='order'>
                <div className="login-page">
                    <div className="form">
                        <button
                            className="cancel-btn"
                            onClick={() => {
                                document.getElementById('order').classList.add('hidden')
                            }}>
                            <div className='cancel-text'>+</div>
                        </button>
                        <div className="login-form">
                            <input id='1' type="text" placeholder="ім'я"/>
                            <input id='2' type='tel' placeholder='номер телефону'/>
                            <input  id = '3' type="email" placeholder="пошта"/>
                            <button className='btn-buy' onClick={()=>{
                                let pr = R.compose(
                                    R.fromPairs,
                                    R.map(x =>  [`product[${x.id}]`, x.count])
                                )(phones)
                                let obj = R.merge({
                                    token: 'BkB1xjHdQ02lctHrbkUT',
                                    name: '12121',
                                    phone: '1212',
                                    email: '2323',
                                }, pr)
                                console.log(obj)
                                sendOrder(
                                    document.getElementById('1').value,
                                    document.getElementById('3').value,
                                    document.getElementById('2').value,
                                    R.compose(
                                        R.fromPairs,
                                        R.map(x =>  [`product[${x.id}]`, x.count])
                                    )(phones)
                                    )
                            }}>Купити</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}


const mapStateToProps = state => {
    return {
        phones: getBasketPhonesWithCount(state),
        totalPrice: getTotalBasketPrice(state)
    }
}
const mapDispatchToProps = {
    removePhoneFromBasket,
    addPhoneFromBasket,
    removeOnePhoneFromBasket,
    sendOrder
}
export default connect(mapStateToProps, mapDispatchToProps)(Basket)
